@import '../../assets/mainstyles.scss';

.rental-main {
	@include flex-column-center;
	gap: 30px;

	@media (max-width: $mobile-size) {
        gap: 15px;
	}

	&__carousel-container {
		width: 100%;
		height: 415px;
		border-radius: 25px;
		object-fit: cover;
		overflow: hidden;

		@media (max-width: $mobile-size) {
			height: 255px;
		}
	}

	&__items {
		display: flex;
		flex-flow: row wrap;
		justify-content: space-between;
		width: 100%;
		
		@media (max-width: $mobile-size) {
			flex-direction: column;
			gap: 15px;
		}

		&__container {
			display: flex;
			flex-direction: column;
			gap: 20px;
			max-width: 75%;

			@media (max-width: $mobile-size) {
				max-width: none;
				gap: 5px;
			}
		
			&__title {
				max-width: calc((100vw - (200 * $base-desktop-width)) * 3/4);

				@media (max-width: $mobile-size) {
					max-width: none;
					line-height: 142.6%;
				}


				&__title {
					color: $main-color;
					font-size: 36px;
					font-weight: 500;

					@media (max-width: $mobile-size) {
						font-size: 18px;
						padding-bottom: 5px;
					}
				}

				&__location {
					font-size: 18px;

					@media (max-width: $mobile-size) {
						font-size: 14px;
					}
				}
			}

			&__host {
				display: flex;
				flex-direction: row;
				align-items: center;
				justify-content: right;
				gap: 10px;

				&__name {
					color: $main-color;
					font-size: 18px;
					text-wrap: wrap;
					text-align: right;

					@media (max-width: $mobile-size) {
						font-size: 12px;
					}
				}
				&__pic {
					width: 64px;
					aspect-ratio: 1/1;
					border-radius: 50%;

					@media (max-width: $mobile-size) {
						width: 32px;
					}
				}
			}

			&__tags {
				display: flex;
				flex-flow: row wrap;
				gap: 15px;

				@media (max-width: $mobile-size) {
					gap: 10px;
				}
			}

			&__rating {
				display: flex;
				flex-direction: row;
				gap: 4px;

				@media (max-width: $mobile-size) {
					gap: 2px;
					height: fit-content;
				}

				& >.star {
					width: 24.75px;
					height: 24px;
					background-repeat: no-repeat;

					@media (max-width: $mobile-size) {
						width: 13.41px;
						height: 13px;
					}

					&.true {
						background-image: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="100%" height="100%" viewBox="0 0 25 24" fill="none"><path d="M13.7212 0.843656C13.4728 0.328088 12.9479 0 12.3714 0C11.7949 0 11.2747 0.328088 11.0216 0.843656L8.00788 7.04452L1.27748 8.03816C0.715055 8.12253 0.246365 8.51623 0.0729491 9.05524C-0.100466 9.59424 0.0401406 10.1895 0.443215 10.5879L5.32697 15.4201L4.17399 22.2491C4.08025 22.8115 4.3146 23.3833 4.7786 23.7161C5.24261 24.0489 5.85659 24.0911 6.36278 23.8239L12.3761 20.6133L18.3894 23.8239C18.8956 24.0911 19.5096 24.0536 19.9736 23.7161C20.4376 23.3786 20.6719 22.8115 20.5782 22.2491L19.4205 15.4201L24.3043 10.5879C24.7073 10.1895 24.8526 9.59424 24.6745 9.05524C24.4964 8.51623 24.0324 8.12253 23.47 8.03816L16.7349 7.04452L13.7212 0.843656Z" fill="%23FF6060"/></svg>');					
					}

					&.false {
						background-image: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="100%" height="100%" viewBox="0 0 25 24" fill="none"><path d="M13.7212 0.843656C13.4728 0.328088 12.9479 0 12.3714 0C11.7949 0 11.2747 0.328088 11.0216 0.843656L8.00788 7.04452L1.27748 8.03816C0.715055 8.12253 0.246365 8.51623 0.0729491 9.05524C-0.100466 9.59424 0.0401406 10.1895 0.443215 10.5879L5.32697 15.4201L4.17399 22.2491C4.08025 22.8115 4.3146 23.3833 4.7786 23.7161C5.24261 24.0489 5.85659 24.0911 6.36278 23.8239L12.3761 20.6133L18.3894 23.8239C18.8956 24.0911 19.5096 24.0536 19.9736 23.7161C20.4376 23.3786 20.6719 22.8115 20.5782 22.2491L19.4205 15.4201L24.3043 10.5879C24.7073 10.1895 24.8526 9.59424 24.6745 9.05524C24.4964 8.51623 24.0324 8.12253 23.47 8.03816L16.7349 7.04452L13.7212 0.843656Z" fill="%23E3E3E3"/></svg>');
					}
				}
			}
		}

		&__host-stars {
			@media (max-width: $mobile-size) {
				display: flex;
				flex-direction: row-reverse;
				align-items: center;
				justify-content: space-between;
				width: 100%;
			}
		}
	}

	&__details {
		width: 100%;
		display: flex;
		flex-direction: row;
		justify-content: space-between;

		@media (max-width: $mobile-size) {
			flex-direction: column;
			gap: 20px;
		}

		&__dropdown {
			width: calc(50% - (76px/2));

			@media (max-width: $mobile-size) {
				width: 100%;
			}

			&__title {
				display: none;
			}
		}
	}
  
}

.rental-pic {
	width: calc(100vw - (200 * $base-desktop-width));
	height: 415px;

	@media (max-width: $mobile-size) {
		width: calc(100vw - (40 * $base-mobile-width));
		height: 255px;
	}

	&__image {
		@include img-cover;
		position: relative;
	}
}